import React from "react";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";

// Schema xc thc cho Yup
const SignupSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, "Tn qu ngn!")
    .max(50, "Tn qu di!")
    .required("Bt buc nhp"),
  email: Yup.string().email("Email khng hp l").required("Bt buc nhp"),
  phone: Yup.string()
    .matches(/^[0-9]+$/, "S in thoi ch cha s")
    .min(10, "S in thoi phi c t nht 10 ch s")
    .required("Bt buc nhp"),
  event: Yup.string().required("Bt buc chn s kin"),
});

// Component FormikForm
const Signup = () => {
  return (
    <Formik
      initialValues={{
        name: "",
        email: "",
        phone: "",
        event: "",
      }}
      validationSchema={SignupSchema}
      onSubmit={(values, { setSubmitting }) => {
        alert(JSON.stringify(values, null, 2));
        setSubmitting(false);
      }}
    >
      {({ values, errors, touched, isSubmitting }) => (
        <Form>
          <Field name="name" placeholder="Tn" />
          {errors.name && touched.name ? <div>{errors.name}</div> : null}

          <Field name="email" placeholder="Email" />
          {errors.email && touched.email ? <div>{errors.email}</div> : null}

          <Field name="phone" placeholder="S in thoi" />
          {errors.phone && touched.phone ? <div>{errors.phone}</div> : null}

          <Field name="event" as="select">
            <option value="">Chn s kin</option>
            <option value="event1">S kin 1</option>
            <option value="event2">S kin 2</option>
            <option value="event3">S kin 3</option>
          </Field>
          {errors.event && touched.event ? <div>{errors.event}</div> : null}

          <button type="submit" disabled={isSubmitting}>
            ng k
          </button>

          {/* Tnh nng preview thng tin */}
          {isSubmitting ? null : (
            <div class="card">
              <header class="card-header">
                <h3 class="card-header-title">Preview Thng Tin</h3>
              </header>
              <div class="card-content">
                <p>Tn: {values.name}</p>
                <p>Email: {values.email}</p>
                <p>S in thoi: {values.phone}</p>
                <p>S kin: {values.event}</p>
              </div>
            </div>
          )}
        </Form>
      )}
    </Formik>
  );
};

export default Signup;
